
.version {
  font-size: 12px;
  position: fixed;
  right: 15px;
  bottom: 15px;
  background: white;
  z-index: 999;
  padding: 5px;
}

#login {
  padding-top: 75px;
  padding-bottom: 75px;
}
#login .login-form-containe {
  width: 100%;
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
